export default {
  shuffle (array) {
    for (var i = array.length - 1; i > 0; i--) {
      var j = Math.floor(Math.random() * (i + 1))
      var temp = array[i]
      array[i] = array[j]
      array[j] = temp
    }
    return array
  },
  userId () {
    return '_' + Math.random().toString(36).substr(2, 9)
  }
}



